import { html } from 'lit';

export default {
  title: 'Mega Puzzle/Button',
  component: 'Button',
  id: 'btn',
  args: { label: "I'm an anchor tag", href: "https://reeceneedham.com", primary: false, openInNewTab: false}
};

export const anchor = {
  args: { label: "I'm an anchor tag", href: "https://reeceneedham.com", openInNewTab: false, primary: true},
  render: ({label, href, openInNewTab, primary}) => {

    return openInNewTab ? 
    html`<a class="${primary ? `primary`:``}" href="${href}" target="_blank" rel="noopener">${label}</a>`:
    html`<a class="${primary ? `primary`:``}" href="${href}">${label}</a>`;
  }
};

export const button = {
  args: { label: "I'm a button!", primary: true},
  render: ({label, primary}) => {
    primary ? `class="primary"` : console.log(primary)
    return html`<button>${label}</button>`
  }
};
